@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  ;
}

body {
  font-family: 'Raleway', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: rgb(255, 255, 255);
  font-size: 16px;
  line-height: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 16px;
  line-height: 18px;
}

h1 {
  font-weight: bold;
  font-size: 42px;
  line-height: 46px;
}

h2 {
  font-weight: bold;
  font-size: 72px;
  line-height: 75px;
}

.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.7);
}

.app::before {
  content: '';
  background: url("./assets/bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.search {
  text-align: center;
}

.searchInput {
  font-family: 'Raleway';
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 22px;
  outline: none;
  padding: 1rem;
  font-size: 18px;
  color: white;
  width: 500px;

}

::placeholder {
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

.container {
  max-width: 700px;
  height: 100%;
  margin: 0 auto;
  padding: 100px 100px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main__title {
  font-size: 72px;
  line-height: 75px;
  font-weight: bold;
  text-align: center;
}

.top {
  width: 100%;
  margin: 1rem auto;
}

.temp {
  margin-bottom: 12px;
}

.desc {
  position: relative;
  left: 0;
  opacity: .7;
}

.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.5);
}

.bottom_item {
  font-weight: bold;
  font-size: 26px;
}

.title {
  margin-bottom: 8px;
}

@media (max-width: 992px) {
  .bottom {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    max-width: 320px;
    height: 100%;
    padding: 2rem 1rem;
  }

  .item {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }

  .title {
    margin-right: 12px;
  }

  .main__title {
    margin-top: 72px;
    font-size: 64px;
    line-height: 68px;
  }

  .margin_item {
    margin-bottom: 32px;
  }

  .searchInput {
    border-radius: 22px;
    outline: none;
    font-size: 14px;
    color: white;
    width: 275px;
  }

  .container {
    max-width: 700px;
    height: 100%;
    margin: 0 auto;
    padding: 50px 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .top {
    margin: 2rem auto;
    max-width: 320px;
    height: 100%;
  }
}